import React from 'react';
import "../style/modal.css";

const Modal = ( props ) => {
    // , ,     
    const { open, close, accept, header } = props;

    //console.log(header)
    return (
        //   openModal  .
        <div className={ open ? 'openModal modal' : 'modal' }>
            { open ? (  
                <section>
                    <header>
                        {header}
                        <button className="close" onClick={close}> &times; </button>
                    </header>
                    <main>
                        {props.children}
                    </main>
                    <footer>
                        <button className="close" onClick={close}> Reject </button>
                        <button className="close" onClick={accept}> Accept </button>
                    </footer>
                </section>
            ) : null }
        </div>
    )
}

export default Modal